.operating__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: hsl(0, 0%, 0%);
  margin: 0;
  padding: 0;
  margin-bottom: 40px;
}

.operating__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
  gap: 20px;
}

.operating__list-item {
  position: relative;
  display: flex;
  align-items: center;
}

